@import "../../styles/variables.scss";

.todo-update-form {
  width: 100%;
  height: 100%;

  .todo-update-name,
  .todo-update-description {
    width: 100%;
    border: none;
    outline: none;
    padding: 16px;
    border-radius: 8px;
  }

  .todo-update-name {
    font-size: 16px;
  }

  .todo-update-description {
    font-size: 14px;
    resize: none;
  }

  &__select {
    padding: 8px 16px;
    width: 100%;
    border-radius: 8px;
    line-height: 1.5;
    background-color: #fff;
  }

  &__input-wrapper:not(:first-child) {
    margin-top: $base * 2;
  }
	
}

.todo-choose {
	cursor: default;
	font-weight: 400;
}